.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}



/* Importar la fuente Lora de Google Fonts */
@import url('https://fonts.googleapis.com/css2?family=Lora:wght@400;700&display=swap');

/* Estilos generales */
.carta-container {
  background-color: #EDE7F6; /* Suave fondo lila */
  padding: 40px;
  border-radius: 20px;
  color: #5D3A9B; /* Tono violeta más suave */
  max-width: 800px;
  margin: 40px auto;
  text-align: center;
  font-family: 'Lora', serif; /* Fuente Lora */
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
  animation: fadeIn 1s ease-in-out;
}

.carta-titulo {
  font-size: 2.8em;
  color: #7E57C2; /* Violeta claro */
  margin-bottom: 20px;
  animation: fadeInDown 1s ease-in-out;
}

.carta-cuerpo {
  font-size: 1.3em;
  line-height: 1.8;
  margin-bottom: 20px;
  animation: fadeIn 2s ease-in-out;
  color: #6A4E99; /* Violeta más apagado */
}

.carta-dias {
  font-weight: bold;
  color: #9B59B6; /* Un tono más cálido */
}

.carta-firma {
  font-size: 1.8em;
  color: #8E44AD; /* Un violeta más elegante */
  margin-top: 30px;
  animation: slideInLeft 2s ease-in-out;
}

@keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}

@keyframes fadeInDown {
  from { opacity: 0; transform: translateY(-50px); }
  to { opacity: 1; transform: translateY(0); }
}

@keyframes slideInLeft {
  from { opacity: 0; transform: translateX(-100px); }
  to { opacity: 1; transform: translateX(0); }
}

.carta-container:hover {
  box-shadow: 0 15px 25px rgba(0, 0, 0, 0.2);
  transition: box-shadow 0.3s ease-in-out;
}

.carta-firma:hover {
  color: #D1B3F1; /* Color más suave al hacer hover */
  transition: color 0.3s ease-in-out;
}

/* Media queries para móviles */
@media only screen and (max-width: 768px) {
  .carta-container {
    padding: 20px;
    margin: 20px;
    border-radius: 15px;
  }

  .carta-titulo {
    font-size: 2.5em;
    margin-bottom: 15px;
  }

  .carta-cuerpo {
    font-size: 1.1em;
    line-height: 1.6;
    margin-bottom: 15px;
  }

  .carta-firma {
    font-size: 1.5em;
    margin-top: 20px;
  }
}

/* Media queries para pantallas aún más pequeñas */
@media only screen and (max-width: 480px) {
  .carta-container {
    padding: 15px;
    margin: 10px;
    border-radius: 10px;
  }

  .carta-titulo {
    font-size: 2em;
    margin-bottom: 10px;
  }

  .carta-cuerpo {
    font-size: 1em;
    line-height: 1.4;
    margin-bottom: 10px;
  }

  .carta-firma {
    font-size: 1.3em;
    margin-top: 15px;
  }
}

.cargando-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh; /* Ocupa toda la altura de la ventana */
  background-color: rgba(255, 255, 255, 0.8); /* Fondo semi-transparente */
}

.spinner {
  border: 8px solid rgba(200, 200, 200, 0.3); /* Color del borde */
  border-left-color: #6200ea; /* Color del spinner */
  border-radius: 50%;
  width: 60px; /* Tamaño del spinner */
  height: 60px; /* Tamaño del spinner */
  animation: spin 1s linear infinite; /* Animación de rotación */
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
